import React from "react";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { useHistory, Redirect } from "react-router-dom";
import {
  CButton,
  CRow,
  CCol,
  CCard,
  CCardBody,
  CCardHeader,
} from "@coreui/react";
import moment from "moment";
import {
  RESET_DATA_EXAM,
  RESET_DATA_SOAL,
  RESET_USER,
} from "../../../redux/actions";
import endpoints from "../../../utils/endpoints";
import request from "../../../utils/request";
import Header from "../Header";

function ExamConfirmation() {
  const dataExam = useSelector((state) => state.dataExam);
  const { profile } = useSelector((state) => state.user);
  const dispatch = useDispatch();
  const history = useHistory();
  const finishTest = async () => {
    let res = null;
    try {
      const config = {
        ...endpoints.startExamUser,
        data: {
          id: dataExam.currentUser.id,
          waktu_selesai: moment().format("YYYY-MM-DD HH:mm"),
          status_ujian: "selesai",
        },
      };
      res = await request(null, config);
      dispatch({ type: RESET_DATA_SOAL, payload: null });
      dispatch({ type: RESET_DATA_EXAM, payload: null });
      dispatch({ type: RESET_USER, payload: null });
      localStorage.clear();
      history.push("/login");
    } catch (error) {
      alert(error.error.response.data.messages);
    }
  };

  const isLoggedIn = () => (profile.nama ? true : false);

  if (!isLoggedIn()) {
    console.log(isLoggedIn());
    return <Redirect to="/login" />;
  }
  return (
    <Header>
      <CRow className="d-flex justify-content-center">
        <CCol md={6}>
          <CCard className="card-rounded shadow border-white d-flex flex-wrap mt-2">
            <CCardHeader className="d-flex direction-row justify-content-between align-items-center card-rounded border-0">
              <h3>Konfirmasi Tes</h3>
            </CCardHeader>
            <CCardBody className="pt-0">
              <div className="mb-4">
                Terimakasih telah berpartisipasi dalam tes ini. Silahkan tekan
                tombol{" "}
                <span className="text-white px-1 bg-success">SELESAI TES</span>{" "}
                untuk mengakhiri tes, atau tekan{" "}
                <span className="text-white px-1 bg-danger">KEMBALI</span> untuk
                kembali ke halaman test.
              </div>
              <div className="d-flex direction-row justify-content-center">
                <CButton
                  block
                  color="danger"
                  className="mx-1"
                  size="lg"
                  shape="pill"
                  onClick={() => finishTest()}
                >
                  SELESAI TES
                </CButton>
                <Link to={"/exam"}>
                  <CButton
                    block
                    color="success"
                    className="mx-1"
                    size="lg"
                    shape="pill"
                  >
                    KEMBALI
                  </CButton>
                </Link>
              </div>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </Header>
  );
}

export default ExamConfirmation;
